<script lang="typescript">
    import {onMount} from 'svelte'

    import jquery from 'jquery'
    // Ref: https://codepen.io/blucube/pen/cudAz

    var knob = jquery('.knob');
var angle = 0;
var minangle = 0;
var maxangle = 270;

onMount(()=>{
    moveKnob('up')
    moveKnob('up')
    moveKnob('up')
    moveKnob('up')
    moveKnob('up')
    moveKnob('up')
    moveKnob('up')
    moveKnob('up')
    moveKnob('up')
    moveKnob('up')
    moveKnob('up')
    moveKnob('up')
    moveKnob('up')
    moveKnob('up')
    moveKnob('up')
    moveKnob('up')
    moveKnob('up')
    moveKnob('up')
    moveKnob('up')
    moveKnob('up')
    moveKnob('up')
    moveKnob('up')
    moveKnob('up')
    moveKnob('up')
})

function moveKnob(direction) {
  
  if(direction == 'up') {
    if((angle + 2) <= maxangle) {
      angle = angle + 2;
      setAngle();
    }
  }
  
  else if(direction == 'down') {
    if((angle - 2) >= minangle) {
      angle = angle - 2;
      setAngle();
    }
  }

}

function setAngle() {

  // rotate knob
  knob.css({
    '-moz-transform':'rotate('+angle+'deg)',
    '-webkit-transform':'rotate('+angle+'deg)',
    '-o-transform':'rotate('+angle+'deg)',
    '-ms-transform':'rotate('+angle+'deg)',
    'transform':'rotate('+angle+'deg)'
  });
  
  // highlight ticks
  var activeTicks = (Math.round(angle / 10) + 1);
  jquery('.tick').removeClass('activetick');
  jquery('.tick').slice(0,activeTicks).addClass('activetick');
  
  // update % value in text
  var pc = Math.round((angle/270)*100);
  jquery('.current-value').text(pc+'%');
  
}

// mousewheel event - firefox
knob.bind('DOMMouseScroll', function(e){
  if(e.originalEvent.detail > 0) {
    moveKnob('down');
  } else {
    moveKnob('up');
  }
  return false;
});

// mousewheel event - ie, safari, opera
knob.bind('mousewheel', function(e){
  if(e.originalEvent.wheelDelta < 0) {
    moveKnob('down');
  } else {
    moveKnob('up');
  }
  return false;
});

</script>
